body{
    font-family: "poppins",sans-serif;
}
:root{
    --red:#d01c28;
    --gray:#2e2e2e;
}
*{
    margin: 0;

}
button{
    all:unset;
    padding: 6px 16px;
    background: var(--red);
    height:32px;
    color: white;
    font-weight: 500;
    border-radius: 50px;
}
.container{
    margin: 0 auto;
    height: 72px;
}
nav{
    display: flex;
    justify-content: space-between;
    max-width: 1280px;
   
    align-items: center;

}
nav ul{
    display: flex;
    list-style: none;
    gap: 24px;

}
.log{

height:40px;
width:40px;
margin-left:20px;
}
/* hero=--- */
.hero{
    display: flex;
    align-items: center;
    height:calc(100vh-72px);
    margin-top: 90px;
}
.brand{
  height:30px;
  width: 30px;
  margin-top:-20px;
  margin-left:10px;
}
.hero-content h1{
    font-weight:800;
    line-height:70px;
    font-size:70px;
    padding-bottom:10px;
    padding:20px;
}
.hero-content p{
    font-weight:600;
    color:var(--gray);
    padding:20px;
    padding-bottom: 10px;
    margin-top:-30px;
    
    max-width:400px;
}
.hero-content{
    margin-top:60px;
}
.hero-button{
    gap:40px;
    display: flex;
    padding:15px;
}
.hero-button button{
    font-size: 24px;
    margin-top:-10px
    
    ;
}
.secondary-button{
    background:transparent;
    color: var(--gray);
    border: 1px solid gray;
}
.shopping p{
    font-size:14px;
    margin-bottom:6px;
    margin-top:-30px;
    padding:20px;


}
.brand-icons{
    display: flex;
    gap:16px;
}
.hero-container{
    display: flex;
    
}

.shoes{
    height :600px;
    width:600px;
}